  }
  
  .content-wrapper {
    width: 90%;
    margin: 50px 0;
  }
  /***** BASE HEADER - FOOTER STYLES *****/
  
  header, 
  footer {
    display: flex;
    align-items: center;
    background-color: #202322;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  header { 
    padding: 15px 0;
  }
  
  header .content-wrapper { 
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    justify-content: center; 
    margin: 0;
  }
  
  .header-divider {
    background: #3a3a3a; 
    height: 1px;
    width: 100%;
  }
  
  .header figure {
    align-items: center;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    width: 60%;
  }
  
  header nav {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    font-size: 1.125em;
    font-weight: 700;
    justify-content: space-around;
    padding-right: 40px;
    text-transform: uppercase;
    width: 60%;
  }
  
  header nav ul {
    display: flex;
    justify-content: space-between; 
    padding-right: 40px;
    width: 100%;
  }
  
  header nav ul li {
    font-size: 1.13em;
  }
  
  footer {
    flex-direction: column;
    text-align: center;
  }
  
  footer .contact { 
    margin-bottom: 50px;
  }
  
  footer content { 
    margin-bottom: 50px;
  }
  
  footer .content-wrapper {
    margin: 50px 0;
    width: 55%;
  }
  
  footer .copywright { 
    margin: 20px 0;
  }
  
  footer p {
    margin: 0;
  }
  
  footer .social-icons{ 
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  
  footer .social-icons p {
    display: flex;
    align-items: center;
    background-color: #527158;
    border-radius: 80%;
    /*! specifics of border-radius property?*/
    height: 40px;
    justify-content: center;
    width: 40px;
  }
  
  footer .social-icons p i {
    font-size: 1.3125em;
  }
  
  
  /***** MOBILE BODY STYLES *****/ 
  
  /*========== Homepage ==========*/
  
  /* HERO */
  .hero-area {
    display: flex;
    background: url('../img/jake-melara-30679-unsplash.jpg') center no-repeat;
    background-size: cover;
    flex-flow: row wrap;
    justify-content: center; 
    min-height: 500px;
  }
  
  .hero-area h1 { 
    font-family: 'Poppins', sans-serif;
    font-size: 2.25em; 
    font-weight: 500;
    line-height: 1.2em;
  }
  
  /* ASIDE */
  aside {
    display: flex;
    align-items: center;
    background-color: #527158;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  aside .sign-up-form { 
    margin-top: 30px;
  }
  
  aside .sign-up-form-description h3 { 
    padding-top: 10px;
  }
  
  input[type=text] { 
    box-sizing: border-box;
    color: #2a423a;
    font-style: italic;
    padding: 1em;
    width: 100%;
  }
  
  input[type=button] {
    background-color: #2a423a;
    border: none;
    color: #e2e3ed;
    font-family: 'Poppins', Helvetica, Arial, sans-serif; 
    font-size: 1.125em;
    font-weight: 500;
    margin-top: 20px;
    padding: 20px 0;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
  }
  
  /* FEATURES */
  .features {
    display: flex;
    background: url('../img/rawpixel-800820-unsplash1.jpg') center no-repeat;
    background-size: cover;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  
  .features article { 
    width: 63%;
    margin: 30px 0;
  }
  
  .features .feature-items {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    justify-content: center; 
  }
  
  .features h2 {
    font-size: 2em;
    margin-bottom: 40px;
  }
  
  .features i { 
    font-size: 3.75em;
    margin-bottom: 40px;
  }
  
  .features p {
    margin: 0; 
    text-align: center;
  }
  
  /* REVIEWS */
  .reviews {
    display: flex;
    background-color: #2a423a;
    flex-flow: row wrap;
    justify-content: center; 
  }
  
  .reviews h2 { 
    font-size: 2.2em;
    line-height: 1.25em;
    margin-bottom: 30px;
    padding-left: 5px; 
  }
  
  .reviews h3 { 
    font-size: 1.8em;
    line-height: 1.5em;
    padding: 20px 0;
    padding-left: 15px;
    width: 60%; 
  }
  
  .reviews p {
    font-size: 1em;
    padding-left: 15px;
  }
  
  /*========== About Page ==========*/
  /* MISSION */
  .mission {
    display: flex;
    align-items: center;
    background-color: #527158;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  .mission h1{ 
    font-family: 'Poppins', sans-serif;
    font-size: 2.25em; 
    font-weight: 500;
    line-height: 1.2em;
  }
  
  /* MAP */
  .map figure {
    height: 0;
    overflow: hidden;
    padding-bottom: 75%;
    position: relative;
    max-width: 100%;
  }
  
  .map iframe { 
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
  
  /* CONTACT */
  .contact-section {
    display: flex;
    align-items: center;
    background-color: #2a423a;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  .contact-content p { 
    margin: 0;
  }
  
  /*========== FAQ Page ==========*/
  /* FAQ */
  .faq {
    display: flex;
    align-items: center;
    background-color: #527158;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  .faq .content-wrapper {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    justify-content: center; 
    margin: 50px 0;
  }
  
  .faq h2 { 
    line-height: 1em;
    margin-bottom: 40px;
  }
  
  .faq content { 
    margin-bottom: 50px;
  }
  
  .faq h4 { 
    line-height: 1.333em;
  }
  
  /* ==========================================================================
     Helper classes
     ========================================================================== */
  
  /*
   * Hide visually and from screen readers
   */
  
  .hidden,
  [hidden] {
